body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.result {
  margin-left: 50px;
  margin-right: 50px;
  padding-top: 20px;
  border-top: 2px solid #56a4a4;
}

.result h3,
.result h4 {
  font-weight: normal;
}
.result em {
  font-weight: bold;
}

.App input,
.App button {
  margin: 20px;
  padding: 10px 20px;
  border: 2px solid gray;
  background-color: #fff;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.App button {
  cursor: pointer;
}
.App button:hover {
  background-color: #000;
  color: white;
  border-color: black;
}

.App {
  min-height: 100vh;
  text-align: center;
  background-color: #282c34;
  color: aliceblue;
  padding: 20px;
}

.App-header {
  margin-bottom: 50px;
}

.App-description {
  margin-bottom: 50px;
  color: #56a4a4;
}

.App-link {
  color: #61dafb;
  text-decoration: none;
}

